const {inputData} = require("./entities-amount.js")

const main = (input) => {
    const len = input.length
    let entity = []
    for (let i = 0; i < len; i++) {
        let data = inputData(input[i])
        entity.push(data)
    }
    console.log(JSON.stringify(entity))
    return entity
}

